
'use client';

import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetDescription, SheetTrigger } from '@/components/ui/sheet';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Bot, LoaderCircle, MapPinned, Siren, Volume2, X } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { getIncidents } from '@/lib/incidents-service';
import { Incident } from '@/lib/types';
import { getDirections, DirectionDetails } from '@/lib/directions-service';
import { getPlaceDetails, autocompletePlaces, PlaceDetails, AutocompletePrediction } from '@/lib/places-service';
import { generateRouteAlert, GenerateRouteAlertOutput } from '@/ai/flows/generate-route-alert-flow';
import { textToSpeech } from '@/ai/flows/text-to-speech-flow';
import { Badge } from './ui/badge';
import { useDebounce } from '@/hooks/use-debounce';

type Location = { lat: number; lng: number; address: string };
type AlertResult = GenerateRouteAlertOutput['detailedAlerts'][0];

export function RouteAlertWidget() {
  const [isOpen, setIsOpen] = useState(false);
  const [from, setFrom] = useState<Location | null>(null);
  const [to, setTo] = useState<Location | null>(null);
  const [isFindingAlerts, setIsFindingAlerts] = useState(false);
  const [alertSummary, setAlertSummary] = useState<string | null>(null);
  const [alertDetails, setAlertDetails] = useState<AlertResult[]>([]);
  const [audioDataUri, setAudioDataUri] = useState<string | null>(null);

  const { toast } = useToast();
  const [allIncidents, setAllIncidents] = useState<Incident[]>([]);

  useEffect(() => {
    const fetchIncidents = async () => {
      const result = await getIncidents();
      if (result.success && result.data) {
        setAllIncidents(result.data);
      } else {
        toast({ variant: 'destructive', title: 'Could not load incidents' });
      }
    };
    fetchIncidents();
  }, [toast]);

  const resetState = () => {
    setAlertSummary(null);
    setAlertDetails([]);
    setAudioDataUri(null);
  }

  const isIncidentOnRoute = (incident: Incident, bounds: google.maps.LatLngBounds, polyline: google.maps.LatLng[]) => {
      if (!bounds.contains({ lat: incident.location.latitude, lng: incident.location.longitude })) {
          return false;
      }
      const incidentLatLng = new google.maps.LatLng(incident.location.latitude, incident.location.longitude);
      return google.maps.geometry.polyUtil.isLocationOnEdge(incidentLatLng, new google.maps.Polyline({ path: polyline }), 1e-3);
  }

  const handleFindAlerts = async () => {
    if (!from || !to) {
      toast({ variant: 'destructive', title: 'Please select start and end locations.' });
      return;
    }
    
    if (typeof window === 'undefined' || !window.google || !window.google.maps.geometry) {
        toast({ variant: 'destructive', title: 'Map service not available', description: 'Google Maps Geometry library is not loaded yet. Please try again in a moment.' });
        return;
    }

    setIsFindingAlerts(true);
    resetState();

    try {
      // 1. Get directions
      const directionsResult = await getDirections(from, to);
      if (!directionsResult.success || !directionsResult.data) {
        throw new Error(directionsResult.error || 'Could not get directions.');
      }
      const route = directionsResult.data;

      // 2. Find incidents on route
      const bounds = new google.maps.LatLngBounds(
          { lat: route.bounds.southwest.lat, lng: route.bounds.southwest.lng },
          { lat: route.bounds.northeast.lat, lng: route.bounds.northeast.lng }
      );
      const decodedPath = google.maps.geometry.encoding.decodePath(route.polyline);
      const incidentsOnRoute = allIncidents.filter(incident => isIncidentOnRoute(incident, bounds, decodedPath));

      if (incidentsOnRoute.length === 0) {
        setAlertSummary('No incidents found on your route. Drive safely!');
        return;
      }

      // 3. Generate AI summary
      const alertResult = await generateRouteAlert({
        incidents: incidentsOnRoute,
        fromAddress: from.address,
        toAddress: to.address,
      });

      setAlertSummary(alertResult.alertSummary);
      setAlertDetails(alertResult.detailedAlerts);
      
      // 4. Generate TTS audio
      const ttsResult = await textToSpeech(alertResult.alertSummary);
      setAudioDataUri(ttsResult.audioDataUri);

    } catch (e: any) {
      console.error('Error finding alerts:', e);
      toast({ variant: 'destructive', title: 'Failed to get alerts', description: e.message });
    } finally {
      setIsFindingAlerts(false);
    }
  };

  const LocationInput = ({ label, value, onSelect, onClear }: { label: string, value: Location | null, onSelect: (location: Location) => void, onClear: () => void }) => {
    const [query, setQuery] = useState('');
    const [predictions, setPredictions] = useState<AutocompletePrediction[]>([]);
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);
    const debouncedQuery = useDebounce(query, 300);

    useEffect(() => {
        const fetchPredictions = async () => {
            if (debouncedQuery.length > 2) {
                const result = await autocompletePlaces(debouncedQuery, null, ['address', 'establishment']);
                if (result.success && result.data) {
                    setPredictions(result.data);
                    setIsDropdownOpen(true);
                }
            } else {
                setPredictions([]);
                setIsDropdownOpen(false);
            }
        };
        fetchPredictions();
    }, [debouncedQuery]);

    const handleSelect = async (prediction: AutocompletePrediction) => {
        setQuery(prediction.description);
        setIsDropdownOpen(false);
        setPredictions([]);
        const detailsResult = await getPlaceDetails(prediction.placeId);
        if (detailsResult.success && detailsResult.data) {
            onSelect({
                lat: detailsResult.data.location.lat,
                lng: detailsResult.data.location.lng,
                address: detailsResult.data.address,
            });
        } else {
            toast({variant: 'destructive', title: 'Could not get location details.'})
        }
    };
    
    if (value) {
        return (
             <div className="flex items-center justify-between p-2 bg-muted rounded-md text-sm">
                <span className="truncate pr-2">{value.address}</span>
                <button onClick={() => { onClear(); setQuery(''); }} className="p-1 rounded-full hover:bg-background"><X className="w-4 h-4" /></button>
            </div>
        )
    }

    return (
        <div className="relative">
            <Input
                placeholder={`Enter ${label} address`}
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                onFocus={() => predictions.length > 0 && setIsDropdownOpen(true)}
            />
            {isDropdownOpen && predictions.length > 0 && (
                <div className="absolute z-50 w-full mt-1 bg-background border rounded-md shadow-lg">
                    {predictions.map(p => (
                        <div key={p.placeId} onClick={() => handleSelect(p)} className="p-2 hover:bg-muted cursor-pointer text-sm">
                            {p.description}
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
  };

  const playAudio = () => {
      if (audioDataUri) {
          const audio = new Audio(audioDataUri);
          audio.play();
      }
  }

  return (
    <Sheet open={isOpen} onOpenChange={setIsOpen}>
      <SheetTrigger asChild>
        <Button
          variant="outline"
          size="icon"
          className="fixed bottom-6 right-6 h-14 w-14 rounded-full shadow-lg z-50 bg-primary text-primary-foreground hover:bg-primary/90"
        >
          <Siren className="h-7 w-7" />
        </Button>
      </SheetTrigger>
      <SheetContent className="flex flex-col">
        <SheetHeader>
          <SheetTitle className="font-headline text-2xl flex items-center gap-2">
            <MapPinned className="w-6 h-6 text-primary" /> Route Alerts
          </SheetTitle>
          <SheetDescription>Get real-time incident alerts for your planned route.</SheetDescription>
        </SheetHeader>
        <div className="space-y-4 py-4">
          <div>
            <Label>Starting Location</Label>
            <LocationInput label="start" value={from} onSelect={setFrom} onClear={() => setFrom(null)} />
          </div>
          <div>
            <Label>Destination</Label>
            <LocationInput label="destination" value={to} onSelect={setTo} onClear={() => setTo(null)} />
          </div>
        </div>
        <Button onClick={handleFindAlerts} disabled={isFindingAlerts || !from || !to}>
          {isFindingAlerts ? (
            <>
              <LoaderCircle className="mr-2 h-4 w-4 animate-spin" />
              Finding Alerts...
            </>
          ) : (
            'Find Alerts'
          )}
        </Button>

        <Separator className="my-4" />
        
        <div className="flex-1 overflow-y-auto pr-2">
            {alertSummary && (
                <Card>
                    <CardHeader>
                        <CardTitle className="flex items-center justify-between text-lg">
                            <div className='flex items-center gap-2'><Bot className="w-5 h-5 text-primary" />AI Summary</div>
                             {audioDataUri && (
                                <Button variant="ghost" size="icon" onClick={playAudio}>
                                    <Volume2 className="w-5 h-5" />
                                </Button>
                            )}
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <p className="text-muted-foreground">{alertSummary}</p>
                    </CardContent>
                </Card>
            )}

            {alertDetails.length > 0 && (
                <div className='mt-4 space-y-3'>
                    <h3 className='font-semibold'>Incidents on Route ({alertDetails.length})</h3>
                    {alertDetails.map(alert => (
                         <Card key={alert.incidentId} className='bg-muted/50'>
                            <CardContent className='pt-4'>
                                 <div className='flex justify-between items-start'>
                                    <p className="font-semibold text-base">{alert.category}</p>
                                    <Badge variant="outline">{alert.severity} Severity</Badge>
                                 </div>
                                 <p className="text-sm text-muted-foreground mt-1">{alert.summary}</p>
                            </CardContent>
                         </Card>
                    ))}
                </div>
            )}
        </div>
      </SheetContent>
    </Sheet>
  );
}
